.container {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    padding: 20px;
  }
  
  .formContainer {
    background-color: white;
    border-radius: 12px;
    padding: 40px;
    width: 100%;
    max-width: 500px;
    box-shadow: 0 5px 7px -1px rgba(0, 0, 0, 0.3), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  }
  
  .headerSection {
    text-align: center;
    margin-bottom: 32px;
  }
  
  .title {
    font-size: 24px;
    font-weight: 700;
    color: #111827;
    margin-bottom: 8px;
  }
  
  .subtitle {
    color: #6b7280;
    font-size: 16px;
  }
  
  .form {
    display: flex;
    flex-direction: column;
    gap: 24px;
  }
  
  .inputGroup {
    display: flex;
    flex-direction: column;
    gap: 6px;
  }
  
  .label {
    font-size: 14px;
    font-weight: 500;
    color: #374151;
  }
  
  .inputWrapper {
    position: relative;
    display: flex;
    align-items: center;
  }
  
  .inputIcon {
    position: absolute;
    left: 12px;
    color: #9ca3af;
    width: 20px;
    height: 20px;
  }
  
  .input {
    width: 100%;
    padding: 12px 12px 12px 40px;
    border: 1px solid #d1d5db;
    border-radius: 8px;
    font-size: 16px;
    transition: border-color 0.2s, box-shadow 0.2s;
  }
  
  .input:focus {
    outline: none;
    border-color: #3b82f6;
    box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
  }
  
  .input::placeholder {
    color: #9ca3af;
  }
  
  .inputError {
    border-color: #ef4444;
  }
  
  .inputError:focus {
    box-shadow: 0 0 0 3px rgba(239, 68, 68, 0.1);
  }
  
  .errorText {
    color: #ef4444;
    font-size: 14px;
  }
  
  .passwordToggle {
    position: absolute;
    right: 12px;
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;
    color: #9ca3af;
  }
  
  .passwordToggleIcon {
    width: 20px;
    height: 20px;
  }
  
  .optionsGroup {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: -8px;
  }
  
  .rememberMe {
    display: flex;
    align-items: center;
    gap: 8px;
    color: #4b5563;
    font-size: 14px;
  }
  
  .checkbox {
    width: 16px;
    height: 16px;
    border-radius: 4px;
    accent-color: #3b82f6;
  }
  
  .forgotPassword {
    color: #3b82f6;
    font-size: 14px;
    text-decoration: none;
    font-weight: 500;
  }
  
  .forgotPassword:hover {
    text-decoration: underline;
  }
  
  .submitButton {
    background-color: #3b82f6;
    color: white;
    padding: 12px;
    border-radius: 8px;
    border: none;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: background-color 0.2s;
  }
  
  .submitButton:hover {
    background-color: #2563eb;
  }
  
  .submitButton:disabled {
    background-color: #93c5fd;
    cursor: not-allowed;
  }
  
  .submitError {
    text-align: center;
    color: #ef4444;
    font-size: 14px;
    margin-top: -8px;
  }
  
  .signupPrompt {
    text-align: center;
    color: #4b5563;
    font-size: 14px;
  }
  
  .signupLink {
    color: #3b82f6;
    text-decoration: none;
    font-weight: 500;
  }
  
  .signupLink:hover {
    text-decoration: underline;
  }
  
  @media (max-width: 480px) {
    .formContainer {
      padding: 24px;
    }
    
    .title {
      font-size: 20px;
    }
    
    .subtitle {
      font-size: 14px;
    }
    
    .input {
      font-size: 14px;
    }
    
    .submitButton {
      font-size: 14px;
    }
  }